body {
  font-family: Lato, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-wrap: break-word;
}

h1 {
  font-size: 3em;
}

h2 {
  font-size: 2em;
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  line-height: 32px;
  color: #162956;
}

h3 {
  font-size: 1.75em;
}

h4 {
  font-size: 1.5em;
  font-family: Montserrat, sans-serif;
  font-weight: 300;
  line-height: 32px;
}

h5 {
  font-size: 1.25em;
}

h6 {
  font-size: 1.125em;
  font-weight: 500;
  font-family: Montserrat, sans-serif;
  margin: 16px 0;
  line-height: 24px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

p {
  line-height: 24px;
}

.small {
  font-size: 0.875em;
}

.section-title {
  color: #1A2C59;
}

.hidden-items {
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
  transition-delay: 0.1s;
}

.red-btn, .white-btn, .black-btn {
  border: 1px solid black;
  text-transform: uppercase;
  padding: 3px;
}

.red-btn {
  background-color: #FF2846;
  border: 1px solid #FF2846;
  color: white;
}

.white-btn {
  color: white;
  border: 1px solid white;
}

.black-btn {
  color: black;
  border: 1px solid black;
  background-color: white;
}

.heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 750px) {
  .heading {
    flex-direction: column;
  }
  .heading h2 {
    text-align: center;
  }
}

@media (max-width: 1190px) {
  .heading {
    margin: 0 10px;
  }
}

.arrow {
  height: 20px;
  width: 29px;
  vertical-align: top;
  margin-left: 9px;
}

.view-link {
  text-transform: uppercase;
  color: #162956;
}

.slick-next {
  right: 50px;
}

.slick-prev {
  left: 50px;
  z-index: 1;
}

.slick-next:before, .slick-prev:before {
  color: #000000;
  font-size: 50px;
}

.hero {
  width: 100%;
  min-width: 100%;
  height: 548px;
  background: url(/images/hero.png);
  background-size: cover;
  background-position: center;
  text-align: center;
  padding-top: 130px;
}

.hero h1 {
  font-weight: 300;
  line-height: 64px;
  text-align: center;
  font-family: Montserrat, sans-serif;
  color: #FFFFFF;
  max-width: 560px;
  margin: 0 auto;
  padding: 0 10px;
}

.hero a {
  padding: 15px 40px;
  line-height: 24px;
  margin: 20px auto;
  display: block;
  width: fit-content;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  position: relative;
}

.expert {
  width: 100%;
  height: 448px;
  background: url(/images/experts.png);
  background-size: cover;
  background-position: center;
}

.expert-card {
  background-color: white;
  width: 60%;
  padding: 20px 40px;
  position: absolute;
  top: -55%;
  box-shadow: 0 0 40px 1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.expert-card h2 {
  margin: 20px 0px 23px;
  color: #162956;
}

.expert-card .red-btn {
  padding: 17px 28px;
  margin: 37px 0 17px;
  display: block;
  width: fit-content;
}

.intercom {
  margin-bottom: -50px;
  text-align: right;
}

@media (max-width: 767px) {
  .hero h1 {
    font-size: 2em;
  }
  .expert-card {
    width: 100%;
    position: relative;
  }
}

@media (max-width: 500px) {
  .hero {
    height: 450px;
    padding-top: 60px;
  }
}

.search-filters {
  display: flex;
}

.search-filters ul {
  width: 33.33%;
}

.wine-preview {
  display: flex;
}

.wine-preview .col-3 {
  width: 33%;
}

.wine-preview .product {
  display: block;
  margin: 0 auto;
}

.wine-preview p.wine-name {
  font-weight: 400;
  font-size: 18px;
}

.wine-preview span {
  font-weight: 500;
}

.wine-preview #cart {
  display: block;
}

select {
  width: 100%;
}

.menu {
  height: 109px;
  width: 100%;
  display: flex;
}

.menu .left-side {
  flex: 1;
  text-align: left;
  margin-left: 30px;
}

.menu .right-side {
  flex: 1;
  text-align: right;
  margin-right: 30px;
}

.menu .logo {
  display: block;
  width: 123px;
  position: relative;
}

.menu .logo img {
  position: absolute;
  top: 20px;
}

.menu ul {
  margin: auto 0;
}

.menu ul li {
  display: inline;
  text-transform: uppercase;
  color: #162956;
  padding-right: 10px;
}

.menu ul .cart-num {
  position: absolute;
  top: 30px;
  right: 30px;
}

.menu ul a:visited {
  color: inherit;
}

@media (max-width: 1023px) {
  .menu {
    height: 150px;
  }
  .menu ul li {
    display: block;
    margin-bottom: 10px;
  }
  .menu ul .cart-num {
    top: 95px;
    right: 40px;
  }
  .menu ul .cart {
    padding-right: 10px;
  }
}

@media (max-width: 500px) {
  .menu {
    margin-bottom: 100px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  .menu .left-side {
    order: 2;
    flex-basis: 50%;
    margin-left: 0px;
    padding: 20px 20px 0 0;
    text-align: right;
  }
  .menu .right-side {
    order: 3;
    flex-basis: 50%;
    margin-right: 0px;
    padding-right: 20px;
  }
  .menu .logo {
    order: 1;
    width: 123px;
    margin-left: 30px;
  }
  .menu .logo img {
    top: 0px;
  }
  .menu ul .cart-num {
    right: 30px;
    top: 210px;
  }
  .menu ul li {
    margin-bottom: 15px;
  }
}

.search-bar {
  width: 100%;
  height: 150px;
  transform: translateY(-75px);
  margin-bottom: -75px;
  padding: 30px 30px 40px 30px;
  background-color: #ffffff;
  position: relative;
}

.search-bar input {
  width: 100%;
  height: 100%;
  box-shadow: 0 0 40px 1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #FFFFFF;
  padding: 0 145px 0 80px;
  background: url(/images/magnify.svg) 28px 23px no-repeat transparent;
  background-size: 33px 33px;
}

.search-bar a {
  position: absolute;
  right: 40px;
  top: 43px;
  padding: 17px 30px;
}

@media (max-width: 767px) {
  .search-bar {
    padding: 0px 0px 20px;
    height: 100px;
  }
  .search-bar a {
    right: 11px;
    top: 11px;
  }
}

@media (max-width: 500px) {
  .search-bar input {
    background: none;
    padding: 0 75px 0 10px;
  }
  .search-bar a {
    visibility: hidden;
  }
  .search-bar a:after {
    background: url(/images/magnifywhite.svg) 50% 50% no-repeat transparent;
    background-color: #FF2846;
    background-size: 33px 33px;
    visibility: visible;
    position: absolute;
    width: 33px;
    height: 33px;
    right: -5px;
    top: -3px;
    content: "";
    padding: 30px;
  }
}

.wine-grid {
  display: flex;
  flex-wrap: wrap;
}

.wine-grid .wine-card {
  flex: 1 0 calc(25% - 16px);
  width: calc(25% - 16px);
  float: left;
  min-width: 215px;
  font-family: Montserrat, sans-serif;
  margin: 8px;
  padding: 16px;
  border: 1px solid white;
  transition: box-shadow 0.3s, border 0.3s;
  transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);
  text-align: center;
}

.wine-grid .wine-card:hover {
  box-shadow: 0 0 40px 1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.wine-grid .wine-card:hover .hidden-items {
  opacity: 1;
}

.wine-grid li > img {
  margin: 0 auto;
  display: block;
}

.wine-grid p.wine-name {
  font-weight: 400;
  font-size: 18px;
  margin: 30px 0 16px;
}

.wine-grid span {
  font-weight: 500;
}

.wine-grid .align-btns {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
}

.wine-grid .align-btns a {
  padding: 7px;
}

.star {
  height: 16px;
}

.star-holder {
  display: inline;
}

.star-holder span {
  padding-left: 8px;
}

.cookbook {
  margin-top: 100px;
}

.cookbook .list-of-recipes {
  width: 100%;
  display: flex;
}

.cookbook article {
  width: 33.33%;
  margin: 10px;
}

.cookbook .img-holder {
  width: 100%;
  height: 350px;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
}

.cookbook .img-holder h2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-size: 48px;
  font-family: Montserrat, sans-serif;
  font-weight: 900;
  color: #FFFFFF;
  margin: 0;
}

.cookbook .descr {
  font-family: Montserrat, sans-serif;
  opacity: 0.8;
}

@media (max-width: 500px) {
  .cookbook {
    margin-top: 30px;
  }
}

@media (max-width: 767px) {
  .heading {
    margin-bottom: 20px;
  }
  .list-of-recipes {
    flex-wrap: wrap;
  }
  .list-of-recipes article {
    width: 100%;
  }
  .img-holder {
    background-size: cover;
  }
}

.fans {
  text-align: center;
  margin: 80px 0 45px;
}

.fans .insta-reel {
  max-width: 1700px;
  margin: 0 auto;
}

.fans .insta-card {
  position: relative;
}

.fans .insta-card h6 {
  margin-top: 25px;
}

.fans .fan {
  width: 100%;
  position: relative;
  padding: 5px;
}

.fans .profile {
  position: absolute;
  left: 50%;
  bottom: 11%;
  transform: translate(-50%, -5%);
}

footer {
  background-color: rgba(215, 219, 230, 0.2);
}

footer .container {
  display: flex;
  padding: 80px 0;
  flex-wrap: wrap;
}

footer .col-4 {
  width: 25%;
  padding: 10px;
}

footer .logo {
  display: block;
  width: 170px;
  height: 168px;
  margin: auto;
}

footer .icons {
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
}

footer .icons a {
  height: 30px;
  width: 30px;
  margin-right: 24px;
}

footer .icons a:last-child {
  margin-right: 0px;
}

footer a {
  color: #000000;
}

footer li {
  margin-bottom: 10px;
}

@media (max-width: 1024px) {
  footer .container {
    padding: 80px 70px;
  }
  footer .col-4 {
    width: 50%;
    text-align: center;
  }
  footer h5 {
    margin-bottom: 15px;
  }
  footer .icons {
    justify-content: center;
  }
  footer .icons img {
    padding: 0 15px 10px;
  }
}

@media (max-width: 767px) {
  footer .col-4 {
    width: 100%;
    padding: 0;
  }
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}
